import React, { useState, useEffect, useCallback } from "react";
import { faCaretDown, faCaretRight } from "@fortawesome/pro-light-svg-icons";
import { motion, AnimatePresence } from "framer-motion";
import Theme from "../../theme";
import { Text } from "../Text";
import { Box } from "../Box";
import { Grid } from "../Grid";
import { Icon } from "../Icon";

interface IAccordionPanelProps {
  /** Unique Id of the panel */
  id: string;
  /** Disabled panel cannot be manually expanded or collapsed. @default false */
  disabled?: boolean;
  /** Heading text of the panel */
  heading: string;
  /** Content of the panel */
  content: React.ReactNode;
  /**
   * True if panel should be expanded by default.
   * If Accordion independent mode is set to false, then only the
   * first panel, set as active, will be expanded.
   * @default false
   */
  defaultActive?: boolean;
  /**
   * Place an action in the panel header.
   * Recommended components are
   *
   * - `<Icon />`, without size prop
   * - `<Text />`, without variant prop
   * - `<Button />, with spacing='none'
   */
  action?: React.ReactNode;
  /**
   * Remove the horizontal spacing in the panel.
   * Useful for placing components with included
   * padding like ContentView (Table, Chart, etc.).
   * @default false
   */
  fullWidth?: boolean;
}

interface IAccordionProps {
  /**
   * Each panel is represents a collapsible section of the accordion
   */
  panels: IAccordionPanelProps[];
  /**
   * Independent panels do not collapse when other panel is expanded.
   * @default false
   */
  independent?: boolean;
  /**
   * Takes the full available height.
   * @experimental Do not use with nested accordion.
   * @caution Does not work in Safari
   * @default false
   */
  fullHeight?: boolean;
}

interface IAccordionOptions {
  expandedIds: string[];
  handleExpand: (panelId: string) => void;
  handleCollapse: (panelId: string) => void;
}

const useAccordion = (props: IAccordionProps): IAccordionOptions => {
  const [expandedIds, setExpandedIds] = useState<string[]>([]);
  useEffect(() => {
    const { independent = false, panels = [] } = props;
    const activePanelIds = panels
      .filter((panel) => (panel.defaultActive ? true : false))
      .map((panel) => panel.id);
    setExpandedIds(
      independent || activePanelIds.length <= 1
        ? activePanelIds
        : [activePanelIds[0]]
    );
  }, [props]);

  const handleExpand = useCallback(
    (panelId: string) => {
      if (props.independent) {
        setExpandedIds((prevIds) =>
          prevIds.indexOf(panelId) > -1 ? prevIds : [...prevIds, panelId]
        );
      } else setExpandedIds([panelId]);
    },
    [props.independent]
  );
  const handleCollapse = useCallback((panelId: string) => {
    setExpandedIds((prevIds) => prevIds.filter((id) => id !== panelId));
  }, []);

  return { expandedIds, handleExpand, handleCollapse };
};

const Panel = (
  props: IAccordionPanelProps &
    IAccordionOptions & {
      fullHeight?: IAccordionProps["fullHeight"];
    }
) => {
  const {
    expandedIds,
    handleExpand,
    handleCollapse,
    heading,
    id,
    action,
    disabled,
    content,
    fullWidth,
    fullHeight = false,
  } = props;
  const isExpanded = expandedIds.indexOf(id) > -1;
  return (
    <Box
      id={"accordion-" + id}
      height={isExpanded && fullHeight ? "100%" : "auto"}
      direction="column"
      alignItems="flex-start"
      className="accordion-panel"
      spacing="none"
    >
      <header
        style={{
          position: "sticky",
          top: 0,
          zIndex: 1,
          width: "100%",
          minHeight: `calc(3.5 * ${Theme.spacing()})`,
          height: "auto",
          padding: `${Theme.spacing("sm")} ${Theme.spacing()}`,
          borderRadius: Theme.radius("sm"),
          cursor: disabled ? "initial" : "pointer",
          background: isExpanded
            ? Theme.colorBackground("rowAlt")
            : "transparent",
        }}
        onClick={
          disabled
            ? undefined
            : () => (isExpanded ? handleCollapse(id) : handleExpand(id))
        }
      >
        <Grid gridTemplate=" auto / auto max-content">
          <Box justifyContent="flex-start">
            <Text
              value={heading}
              variant="strong"
              color={isExpanded ? "primary" : "secondary"}
            />
          </Box>
          {(action || !disabled) && (
            <Box justifyContent="flex-end" spacing="md">
              {action && <div>{action}</div>}
              {!disabled && (
                <Icon icon={isExpanded ? faCaretDown : faCaretRight} />
              )}
            </Box>
          )}
        </Grid>
      </header>
      <AnimatePresence initial={false}>
        {isExpanded && (
          <motion.section
            className="accordion-panel-content"
            key="content"
            style={{
              width: "100%",
              // borderBottom: `1px solid`,
              // borderBottomColor: Theme.colorOutline(),
            }}
            initial="collapsed"
            animate="expanded"
            exit="collapsed"
            variants={{
              expanded: {
                opacity: 1,
                height: fullHeight ? "100%" : "auto",
              },
              collapsed: {
                opacity: 0,
                height: 0,
              },
            }}
            transition={Theme.transition()}
          >
            <div style={{ padding: fullWidth ? 0 : Theme.spacing() }}>
              {content}
            </div>
          </motion.section>
        )}
      </AnimatePresence>
    </Box>
  );
};

/**
 * Accordion
 * ---
 * A set of collapsible panels set in vertical orientation.
 * Can be used for displaying multiple set of information.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Accordion
 *  panels={[
 *    {
 *      id: "panel1",
 *      heading: "Panel heading",
 *      content: <Panel1 />
 *    },
 *  ]}
 * />
 */
const Accordion = (props: IAccordionProps) => {
  const accordionOptions = useAccordion(props);

  return (
    <Box
      direction="column"
      spacing="md"
      height={props.fullHeight ? "100%" : "auto"}
      className="accordion"
    >
      {props.panels.map((panel) => (
        <Panel
          key={panel.id}
          {...panel}
          {...accordionOptions}
          fullHeight={props.fullHeight}
        />
      ))}
    </Box>
  );
};

export { Accordion, IAccordionProps, IAccordionPanelProps };
// export default Accordion;
