// Sample:MainView
import React from "react";
import { TransferShuttle } from "@faharmony/core";
import { ContentView } from "@faharmony/template";

export default () => {
  const [shownCols, setShownCols] = React.useState([
    { id: "1", value: "Item 1" },
    { id: "2", value: "Item 2" },
  ]);
  const [hiddenCols, setHiddenCols] = React.useState([
    { id: "3", value: "Item 3" },
    { id: "4", value: "Item 4" },
  ]);

  const handleHide = (items: { id: string; value: string }[]) => {
    items.forEach((item) => {
      setShownCols((cols) => cols.filter((col) => col.id !== item.id));
      setHiddenCols((cols) => [...cols, item]);
    });
  };
  const handleShow = (items: { id: string; value: string }[]) => {
    items.forEach((item) => {
      setHiddenCols((cols) => cols.filter((col) => col.id !== item.id));
      setShownCols((cols) => [...cols, item]);
    });
  };

  return (
    <ContentView headerTitle="Transfer Shuttles">
      <TransferShuttle
        shuttle1={{
          heading: "Hidden columns",
          items: hiddenCols,
          handleAcceptTransfer: handleHide,
        }}
        shuttle2={{
          heading: "Visible columns",
          items: shownCols,
          handleAcceptTransfer: handleShow,
        }}
      />
    </ContentView>
  );
};
