import styled from "@emotion/styled";
import Theme from "../../theme";

interface ISkeletonProps {
  height?: React.CSSProperties["height"];
  width?: React.CSSProperties["width"];
}

const Skeleton = styled.div<ISkeletonProps>`
  height: ${(p) => p.height || "100%"};
  width: ${(p) => p.width || "100%"};

  animation-duration: 5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;

  background: ${Theme.props.colorBackground.elevated};
  background: linear-gradient(
    to right,
    ${Theme.props.colorBackground.disabled} 8%,
    ${Theme.props.colorBackground.base} 30%,
    ${Theme.props.colorBackground.disabled} 60%
  );
  border-radius: ${Theme.props.radius.sm};

  @keyframes placeHolderShimmer {
    0% {
      background-position: 0px 0;
    }
    100% {
      background-position: 100em 0;
    }
  }
`;

export { Skeleton, ISkeletonProps };
