import React from "react";
import { PageView } from "@faharmony/template";
import { Table } from "@faharmony/table";
import { useTimeout, Tag } from "@faharmony/core";
import { faTerminal } from "@fortawesome/pro-light-svg-icons";

interface IDataProps {
  name: string;
  age: number;
  category: string;
}

const TableView = () => {
  const [showContent, setShowContent] = React.useState(false);
  useTimeout(() => setShowContent(true), 1000);

  const tableData: IDataProps[] = React.useMemo(
    () =>
      showContent
        ? [
            { name: "Siddhant Gupta", age: 25, category: "Developer" },
            { name: "Other person", age: 35, category: "Misc" },
          ]
        : [],
    [showContent]
  );

  const tableColumns: any[] = React.useMemo(
    () => [
      {
        accessor: "name",
        Header: "Full name",
        disableFilter: false,
      },
      {
        accessor: "category",
        Header: "Category",
        disableFilter: true,
        align: "center",
        Cell: ({ value }: any) => <Tag>{value}</Tag>,
      },
      {
        accessor: "age",
        Header: "Age",
        disableFilter: true,
        align: "right",
      },
    ],
    []
  );

  const handleRowClick = (rowObject: IDataProps) => console.log(rowObject);

  const singleRowActions = (rowObject: IDataProps) => [
    {
      id: "log",
      icon: faTerminal,
      tooltip: "Log",
      onClick: () => console.log(rowObject),
    },
  ];

  return (
    // <PageView heading="Table">
    <Table<IDataProps>
      tableHeading="Table"
      data={tableData}
      columns={tableColumns}
      handleRowClick={handleRowClick}
      singleRowActions={singleRowActions}
      enableSorting
      enableFiltering
      enablePagination
      enableMultipleRowSelect
      enableColumnVisibilitySelector
      exportTable
      isLoading={!showContent}
    />
    // </PageView>
  );
};

export default () => (
  <PageView heading="Table">
    <TableView />
  </PageView>
);
