import { CSSProperties } from "react";
import { Config } from "webfontloader";

export interface ITextColors {
  primary: string;
  secondary: string;
  disabled: string;
  inverse: string;
}

export interface IBackgroundColors {
  base: string;
  elevated: string;
  overlay: string;
  disabled: string;
  selected: string;
  hover: string;
  inverse: string;
  rowAlt: string;
}

export interface IOutlineColors {
  border: string;
  borderHover: string;
  borderActive: string;
  shadow: string;
  shadowHover: string;
  shadowActive: string;
}

export interface ITintColors {
  accent: string;
  red: string;
  orange: string;
  yellow: string;
  green: string;
  blue: string;
  purple: string;
  black: string;
  white: string;
}

export interface IThemeColorsProps {
  text: ITextColors;
  background: IBackgroundColors;
  outline: IOutlineColors;
  tint: ITintColors;
}

export interface IThemeTextStyleProps {
  fontSize: NonNullable<CSSProperties["fontSize"]>;
  fontWeight:
    | 100
    | 200
    | 300
    | 400
    | 500
    | 600
    | 700
    | 800
    | 900
    | "normal"
    | "bold";
  lineHeight: CSSProperties["lineHeight"];
  letterSpacing: CSSProperties["letterSpacing"];
  textTransform: CSSProperties["textTransform"];
}

export interface ISizeMultiplierProps {
  xs: number;
  sm: number;
  md: number;
  lg: number;
  xl: number;
}

export interface ICodeStyleProps {
  fontConfig: Config;
  style: IThemeTextStyleProps;
}

export interface ITextStyleProps {
  fontConfig: Config;
  h1: IThemeTextStyleProps;
  h2: IThemeTextStyleProps;
  h3: IThemeTextStyleProps;
  h4: IThemeTextStyleProps;
  h5: IThemeTextStyleProps;
  h6: IThemeTextStyleProps;
  strong: IThemeTextStyleProps;
  p: IThemeTextStyleProps;
  small: IThemeTextStyleProps;
}

export interface IThemeProps {
  lightColors: IThemeColorsProps;
  darkColors: IThemeColorsProps;
  baseRadius: number;
  baseSpacing: number;
  sizeMultiplier: ISizeMultiplierProps;
  code: ICodeStyleProps;
  text: ITextStyleProps;
}

export type SMLType = "xs" | "sm" | "md" | "lg" | "xl" | "none";

export const isThemeTextStyle = (
  obj: IThemeTextStyleProps | Config
): obj is IThemeTextStyleProps =>
  (obj as IThemeTextStyleProps).fontSize !== undefined;

export type TintType =
  | "accent"
  | "red"
  | "orange"
  | "yellow"
  | "green"
  | "blue"
  | "purple"
  | "black"
  | "white"
  | "danger"
  | "warning"
  | "success"
  | "info"
  | "inverse";
