import { useReducer, useCallback } from "react";

interface StateProps<T> {
  past: T[];
  present: T;
  future: T[];
}

// Initial state that we pass into useReducer
const initState: StateProps<any> = {
  // Array of previous state values updated each time we push a new state
  past: [],
  // Current state value
  present: undefined,
  // Will contain "future" state values if we undo (so we can redo)
  future: []
};

// Our reducer function to handle state changes based on action
const reducer = <T>(
  state: StateProps<T>,
  action: { type: string; newPresent?: T; initialPresent?: T }
): StateProps<T> => {
  const { past, present, future } = state;

  switch (action.type) {
    case "UNDO":
      const previous = past[past.length - 1];
      const newPast = past.slice(0, past.length - 1);

      return {
        past: newPast,
        present: previous,
        future: [present, ...future]
      };
    case "REDO":
      const next = future[0];
      const newFuture = future.slice(1);

      return {
        past: [...past, present],
        present: next,
        future: newFuture
      };
    case "SET":
      const { newPresent } = action;

      if (newPresent === present) {
        return state;
      }
      if (newPresent)
        return {
          past: [...past, present],
          present: newPresent,
          future: []
        };
      else return state;
    case "CLEAR":
      const { initialPresent } = action;
      if (initialPresent)
        return {
          ...initState,
          present: initialPresent
        };
      else return state;

    default:
      return state;
  }
};

type Reducer<T> = (prevState: StateProps<T>, action: any) => StateProps<T>;

/**
 * This hook makes it really easy to add undo/redo functionality to your app.
 * @author Gabe Ragland <> https://github.com/gragland
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @param {any} initialState - default: {}
 * @example const { currentState, pastStates, futureStates, allStates, updateState, undoState, redoState, clearState, canUndoState, canRedoState } = useUndo({});
 */
export const useUndo = <T extends object>(initialState: T) => {
  const newInitState: StateProps<T> = {
    ...initState,
    present: initialState || {}
  };
  const [state, dispatch] = useReducer<Reducer<T>>(reducer, newInitState);

  const canUndoState = state.past.length !== 0;
  const canRedoState = state.future.length !== 0;

  // Setup our callback functions
  // We memoize with useCallback to prevent unnecessary re-renders

  const undoState = useCallback(() => {
    if (canUndoState) {
      dispatch({ type: "UNDO" });
    }
  }, [canUndoState, dispatch]);

  const redoState = useCallback(() => {
    if (canRedoState) {
      dispatch({ type: "REDO" });
    }
  }, [canRedoState, dispatch]);

  const updateState = useCallback(
    newCurrent => dispatch({ type: "SET", newPresent: newCurrent }),
    [dispatch]
  );

  const clearState = useCallback(
    () => dispatch({ type: "CLEAR", initialPresent: initialState }),
    [dispatch]
  );

  const allStates: StateProps<T> = state;
  const pastStates: StateProps<T>["past"] = state.past;
  const futureStates: StateProps<T>["future"] = state.future;
  const currentState: StateProps<T>["present"] = state.present;

  // If needed we could also return past and future state
  return {
    currentState,
    pastStates,
    futureStates,
    allStates,
    updateState,
    undoState,
    redoState,
    clearState,
    canUndoState,
    canRedoState
  };
};
