import React from "react";
import { useHistory } from "react-router-dom";
import i18next, { i18n } from "i18next";
import { useResponsive } from "@faharmony/core";
import {
  Navigation,
  INavigationProps,
  INavSecondaryItem,
} from "@faharmony/navigation";
import { IRenderProps, IModuleProps, ISubModuleProps } from "../types";
import { useCheckUserForRolesCallback } from "../helpers";

const useMenu = (Modules: IModuleProps[], i18n?: i18n) => {
  const history = useHistory();
  const checkRoles = useCheckUserForRolesCallback();
  const menu: INavigationProps["menu"] = [];

  const generateSecondaryItem = (
    subModule: ISubModuleProps,
    Module: IModuleProps
  ): INavSecondaryItem => {
    const { id, label, icon, trailingIcon, trailingIconTint } = subModule;
    const subModuleLocation = `/${Module.id}/${id}`;
    return {
      id,
      label:
        i18next.exists(`${Module.id}:${label}`) && i18n
          ? i18n.t(`${Module.id}:${label}`)
          : label,
      icon,
      trailingIcon,
      trailingIconTint,
      handleClick: () => history.push(subModuleLocation),
      isSelectedCallback: () => {
        const locSplit = window.location.hash.split(/\/(?=.)/);
        return locSplit.length > 2 && locSplit[2].startsWith(subModule.id);
      },
    };
  };

  Modules.forEach((Module) => {
    const { id, label, icon, roles, subModules } = Module;
    const moduleLocation = `/${id}`;
    if (checkRoles(roles))
      menu.push({
        id,
        label:
          i18next.exists(`${id}:moduleName`) && i18n
            ? i18n.t(`${Module.id}:moduleName`)
            : label,
        icon,
        handleClick: () => history.push(moduleLocation),
        isSelectedCallback: () => {
          const locSplit = window.location.hash.split(/\/(?=.)/);
          return (
            locSplit.length === 2 && locSplit[1].startsWith(id)
            // locSplit.length > 1 && locSplit.length < 3 && locSplit[1].startsWith(id)
          );
        },
        subItems: subModules?.map((subModule) =>
          generateSecondaryItem(subModule, Module)
        ),
      });
  });

  return menu;
};

export const AppNavigation = ({
  Modules,
  appConfig,
  multilingual,
}: IRenderProps) => {
  const menu = useMenu(Modules, multilingual);
  const { isMobile } = useResponsive();
  return (
    <Navigation
      heading={appConfig.name}
      shortHeading={appConfig.shortName}
      menu={menu}
      actions={appConfig.actions}
      isMenuCollapsedDefault={menu.length === 0}
      isHeaderVisible={!isMobile}
    />
  );
};

export default AppNavigation;
