/**
 * localStorageKeys object used to manage all available
 * key-value pair stored in browser's local storage.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */

const localStorageKeysList = [
  "darkMode",
  "menuCollapsed",
  "i18nextLng",
] as const;

export type LocalStorageKey = typeof localStorageKeysList[number];
const asLocalStorageKey = <T extends { [key: string]: LocalStorageKey }>(
  arg: T
): T => arg;

export const localStorageKeys = asLocalStorageKey({
  darkMode: "darkMode",
  languageCode: "i18nextLng",
  menuCollapsed: "menuCollapsed",
});

export const getLocalStorageValue = (key: LocalStorageKey) =>
  localStorage.getItem(key);

export const setLocalStorageValue = (key: LocalStorageKey, value: string) =>
  localStorage.setItem(key, value);

export const removeLocalStorageValue = (key: LocalStorageKey) =>
  localStorage.removeItem(key);

export const clearLocalStorage = () => localStorage.clear();
