import React from "react";
import { v1 as nextId } from "uuid";
import {
  faSortAlt,
  faSortAmountDown,
  faSortAmountUpAlt,
} from "@fortawesome/pro-light-svg-icons";
import { ColumnInstance } from "react-table";
import { Icon, Theme } from "@faharmony/core";
import { TableChildrenProps } from "../types";

// enableSorting ? column.getSortByToggleProps() : {}
interface TableHeadProps<T extends object> extends TableChildrenProps<T> {
  enableFilter: boolean;
}

const handleSort = <T extends object>(column: ColumnInstance<T>) => {
  if (column.isSorted) {
    if (column.isSortedDesc) column.clearSortBy();
    else column.toggleSortBy(true, false);
  } else column.toggleSortBy(false, false);
};

export const TableHead = <T extends object>({
  tableInstance,
  tableProps,
  enableFilter,
}: TableHeadProps<T>) => {
  const { headerGroups } = tableInstance;
  const { enableSorting = false, enableMultipleRowSelect } = tableProps;

  return (
    <thead>
      {headerGroups.map((headerGroup) => (
        <React.Fragment key={nextId()}>
          <tr {...headerGroup.getHeaderGroupProps()} key={nextId()}>
            {headerGroup.headers.map((column, index) => {
              const sortColumn = enableSorting && !column.disableSortBy;
              return (
                <th
                  key={nextId()}
                  {...column.getHeaderProps()}
                  title=""
                  style={{
                    width: column.width !== 150 ? `${column.width}px` : "auto",
                    minWidth: column.minWidth + "px",
                    maxWidth: column.maxWidth + "px",
                  }}
                  className={column.className}
                >
                  <div
                    style={{
                      textAlign:
                        index === (enableMultipleRowSelect ? 1 : 0)
                          ? undefined
                          : column.align,
                    }}
                  >
                    <h5 style={{ marginRight: sortColumn ? "20px" : 0 }}>
                      {column.render("Header")}
                    </h5>
                  </div>
                  {sortColumn && (
                    <div
                      className="table-header-sort"
                      title="Sort column"
                      onClick={() => handleSort<T>(column)}
                    >
                      {column.isSorted ? (
                        <Icon
                          icon={
                            column.isSortedDesc
                              ? faSortAmountDown
                              : faSortAmountUpAlt
                          }
                          color={Theme.colorText("primary")}
                        />
                      ) : (
                        <Icon icon={faSortAlt} />
                      )}
                    </div>
                  )}
                </th>
              );
            })}
          </tr>
          {enableFilter ? (
            <tr className="filterRow">
              {headerGroup.headers.map((column) => (
                <th key={nextId()}>
                  {column.canFilter ? column.render("Filter") : null}
                </th>
              ))}
            </tr>
          ) : null}
        </React.Fragment>
      ))}
    </thead>
  );
};

export default TableHead;
