import React, { useState } from "react";
import { Trigger, Theme } from "@faharmony/core";
import { motion, AnimatePresence } from "framer-motion";
import { IAppSideDrawerProps } from "../types";
import { SummaryView } from "../views";

const SideDrawerTrigger = new Trigger<IAppSideDrawerProps | undefined>();

const width = 300;

const SideDrawer = () => {
  const [sideView, setSideView] = useState<IAppSideDrawerProps>();
  SideDrawerTrigger.useSubscribe(setSideView);

  // const { children, ...summaryViewProps } = sideView;
  return (
    <AnimatePresence>
      {sideView && (
        <motion.div
          style={{
            zIndex: Theme.zIndex("docked"),
            boxShadow: Theme.shadow("float"),
            backgroundColor: Theme.colorBackground("base"),
          }}
          initial={{ width: 0 }}
          animate={{ width }}
          exit={{ width: 0 }}
          transition={Theme.transition()}
        >
          <SummaryView {...sideView} onClose={() => setSideView(undefined)} />
        </motion.div>
      )}
    </AnimatePresence>
  );
};

/**
 * setAppSideDrawer
 * ---
 * Use this callback to set content is App's side drawer. The drawer uses a summaryView to display content.
 * The information passed should be an object of type - `IAppSideDrawerProps`.
 *
 * The drawer has it's own close mechanism. It can't be closed from outside.
 */
export const setAppSideDrawer = SideDrawerTrigger.publish;
export const resetAppSideDrawer = () => SideDrawerTrigger.publish(undefined);
export default SideDrawer;
