import React from "react";
import { HashRouter as Router } from "react-router-dom";
import { KeycloakInstance } from "keycloak-js";
import { KeycloakContext, ToastProvider } from "@faharmony/core";
import Div100vh from "react-div-100vh";
import { ApolloProvider } from "@apollo/react-hooks";
import ApolloClient from "apollo-boost";

/**
 * AppWrapper
 * ---
 * Wraps the App with required context.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const AppWrapper = (props: {
  children: React.ReactNode;
  keycloakInstance: KeycloakInstance;
}) => {
  const { children, keycloakInstance } = props;

  //GraphQL
  const faGraphQlClient = new ApolloClient({
    request: (operation) => {
      operation.setContext(({ headers }: any) => {
        const token = keycloakInstance.token;
        return {
          headers: {
            ...headers,
            authorization: token ? `Bearer ${token}` : "",
          },
        };
      });
    },
  });

  return (
    <KeycloakContext.Provider value={keycloakInstance}>
      <Router>
        <ToastProvider>
          <ApolloProvider client={faGraphQlClient}>
            <Div100vh id="full-height-wrapper">{children}</Div100vh>
          </ApolloProvider>
        </ToastProvider>
      </Router>
    </KeycloakContext.Provider>
  );
};

export default AppWrapper;
